/* src/styles/Projects.css */

.projects-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px;
  padding: 20px;
  animation: fadeIn 0.5s ease-in-out;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(-10px); }
  to { opacity: 1; transform: translateY(0); }
}

.project-btn {
  padding: 12px 20px;
  background-color: #112240;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  font-size: 16px;
  transition: all 0.3s ease;
}

.project-btn:hover {
  background-color: #1d3557;
  transform: scale(1.05);
}

/* Dark Mode */
body.dark-mode {
  background-color: #121212;
  color: white;
}

.toggle-theme {
  padding: 8px 12px;
  margin: 10px;
  border: none;
  background: #1d3557;
  color: white;
  cursor: pointer;
  border-radius: 5px;
}
